.product {
  width: 260px;
  margin: 0 auto;
}

.product__link {
  text-decoration: none;
}

.product__image {
  display: block;
  margin: 0;
}

.product__wrapper {
  box-sizing: border-box;
  border: 1px solid #ececec;
}

.product__title {
  margin: 0 auto;
  padding: 28px 28px 5px;

  @include fonts(700, 17px, $color-general, 141%);
}

.product__description {
  margin: 0 28px 17px;

  @include fonts(400, 13px, $color-secondary, 231%);
}

.product__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 204px;
  margin: 0 28px;
  padding: 22px 0 26px;

  border-top: 1px solid #ececec;
}

.product__price {
  @include fonts(700, 15px, $color-general, 160%);
}

.product__button {
  background-color: #ffffff;
  border: none;

  cursor: pointer;
}

@media (min-width: 768px) {
  .product {
    display: flex;
    width: 670px;
  }

  .product__wrapper {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }

  .product__inner {
    margin-top: auto;
    width: 268px;
  }
}

@media (min-width: 1150px) {
  .product {
    width: 316px;
    flex-direction: column;
  }
}
